import { Component } from "@angular/core";
import {
  IonicPage,
  NavController,
  NavParams,
  LoadingController,
  ToastController,
  AlertController
} from "ionic-angular";
import { Validators, FormBuilder, FormGroup } from "@angular/forms";
import { HkApiproviderProvider } from "../../providers/hk-apiprovider/hk-apiprovider";

@IonicPage()
@Component({
  selector: 'page-feedback',
  templateUrl: 'feedback.html',
})
export class FeedbackPage {

  private todo: FormGroup;
  public userDetails: any;

  postData = {
    token: "",
    user_id: "",
    fmsg: ""
  };

  resposeData: any;
  ticket: String;
  year: number;

  constructor(
    public alertCtrl: AlertController,
    public navCtrl: NavController,
    public auth: HkApiproviderProvider,
    public toastCtrl: ToastController,
    public formBuilder: FormBuilder,
    public navParams: NavParams,
    public loadingCtrl: LoadingController,
  ) {
    this.postData.token = this.auth.gettoken();
    if (localStorage.getItem('userData')) {
      const data = JSON.parse(localStorage.getItem('userData'));
      this.userDetails = data.userData;
    }
    const date = new Date();
    this.year = date.getFullYear();
    this.todo = this.formBuilder.group({
      fmsg: ["", Validators.required]
    });
  }

  logForm() {

    let zest = this.loadingCtrl.create({
      content: "Sending Feedback..",
      duration: 20000,
      spinner: 'crescent'
    });

    zest.present();

    this.postData.fmsg = this.todo.value.fmsg;

    this.auth.postData(this.postData, "sendfeedback").then(
      result => {
        zest.dismiss();
        this.resposeData = result;
        if (this.resposeData.success) {
          this.ticket = this.resposeData.success;
          this.feedbacksend();
          if (this.navCtrl.canGoBack()) {
            this.navCtrl.pop();
          } else {
            this.navCtrl.setRoot('HomePage');
          }
        } else {
          console.log("Error");
        }
      },
      err => {
        zest.dismiss();
      }
    );
  }

  feedbacksend() {
    let alert = this.alertCtrl.create({
      title: "Feedback Send",
      subTitle: "Thanks for Giving Feedback!",
      buttons: ["Close"]
    });
    alert.present();
  }
  openUrl(url){
    window.open(url)
    // let parseUrl:any =  this.sanitizer.bypassSecurityTrustResourceUrl(url)
    // window.open(parseUrl);
  }

  onclickHome() {
    this.navCtrl.setRoot('HomePage');
  }

  openContact() {
    this.navCtrl.push('ContactPage');
  }

  openfeedback() {
    this.navCtrl.push('FeedbackPage');
  }
  openterms() {
    this.navCtrl.push('TermsPage');
  }

  openprivacy() {
    this.navCtrl.push('TermsPage');
  }
  
  checkLogin() {
    if (localStorage.getItem('userData')) {
      return true;
    }
    return false;
  }
}




